import React from "react";
import Dropdown from "../DropDownButton";
import { Bar } from "react-chartjs-2";

const HistoryCard = ({ yearlyOptions, chartData, ops }) => {
  // Function to process chartData and set backgroundColor
  const processedChartData = () => {
    // Deep copy chartData to avoid direct mutation
    const newChartData = JSON.parse(JSON.stringify(chartData));

    // Assuming the actual values are in the first dataset and goals in the second dataset
    const actualData = newChartData.datasets[0].data;
    const goalData = newChartData.datasets[1].data;

    // Create backgroundColor array based on the actual vs goal comparison
    const backgroundColors = actualData.map((actual, index) => {
      return actual >= goalData[index] ? "#64B864" : "#DA615C";
    });

    // Set the backgroundColors for the actual dataset
    newChartData.datasets[0].backgroundColor = backgroundColors;

    return newChartData;
  };

  return (
    <div className="flex-item big-card">
      <div className="col yearly board">
        <div className="dashboard-title">
          <p className="boardTitle">12 Month History</p>
          <Dropdown options={yearlyOptions} />
        </div>
        <div className="col yearly-calendar">
          {/* Use processedChartData instead of directly passing chartData */}
          <Bar data={processedChartData()} options={ops} />
        </div>
      </div>
    </div>
  );
};

export default HistoryCard;
